*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
   
}
body{
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: 'Jost', sans-serif;
}
html{
    font-size: 100%;
    height: 100%;
    background: #eee;
}
.box{
    width: 25vw;
    height: 58vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate(0 , 0, 0);
    min-width: 20rem;
    min-height: 35rem;
}
.inputData{
width: 100%;
height: 20%;
position: relative;
}
.inputFeild{
    position: absolute;
left: 17%;
top: 25%;
   width: 70%;
   height: 2rem;
   border: 2px  solid gray;
   border-radius: 5px;
   font-size: medium;

}
.wave{
    opacity: 0.3;
    position: absolute;
    top : 120%;

    left:50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;

}
.wave.-three{
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave.-two{
    animation: drift 7000ms infinite linear;
    opacity: 0.1;
    z-index: 3 !important;
}

.box:after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
 z-index: 11;
transform: translate3d(0 ,0 , 0)
}
@keyframes drift {
from{transform: rotate(0deg);}  
from{transform: rotate(360deg);}  
}

.info{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 45%;
    z-index: 4;

}
.location{
    margin-top: 1.5rem;
    text-align: center;
    font-weight: 800;
    font-size: 3rem;
    font-weight: 400;
}
.fa-street-view{
    animation: rotate 3s linear infinite alternate;
}
@keyframes rotate {
    from{
        transform: translate(-0.7rem);
    }
    to{
        transform: translate(0.2rem);
    }
}

.temp{
    margin-top: 1.5rem;
    text-align: center;
    font-size: 3rem;
}
.tempmin_max{
    text-align: center;
    margin-top: 0.3rem;
    font-weight: 300;
    font-size: 1.2rem;
    color: #57606f;
}
#weathercon{
    height: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}
#weathercon .fas{
    font-size: 6rem;
animation: fas-anime 3s linear infinite alternate
}
@keyframes fas-anime {
    from{
        transform: scale(0.8)
    }
    top{transform: scale(1.2);}
}


p{
   margin-left: 33%;
   font-size: larger;
}

@media screen and (max-width : 600px){

    .box{width: 60vw;
    height: 80vh;
    
}
    .wave{
        top : 120%
    }
    #whethercon{
        font-size: 5rem;
    }
    .info{
        font-size: 1.5rem;
    }
}
